import { Component, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { DataService } from '../services/data.service';
import { ValidationService } from '../validation/validation.service';

@Component({
  selector: 'katana-chips',
  templateUrl: './chip.component.html',
  styleUrls: ['./chip.component.scss']
})

export class ChipComponent implements OnDestroy {
  @Input() public title: string;
  @Input() public model: string[];
  @Input() public placeholder: string = 'Nháº­p thÃ´ng tin';
  @Input() public maxItems: number;
  @Input() public disabled: boolean;
  @Input() public validationName: string;
  @Input() public validationScope: string;
  @Input() emitNullOnDestroy: boolean = false;
  @Input() validationService: ValidationService;
  @Output() public modelChange: EventEmitter<string[]> = new EventEmitter();
  @Output() public onAdd: EventEmitter<any> = new EventEmitter();
  @Output() public onRemove: EventEmitter<any> = new EventEmitter();
  @Output() public onBlur: EventEmitter<any> = new EventEmitter();

  constructor(protected dataService: DataService) { }

  ngOnDestroy(): void {
    if (this.emitNullOnDestroy === true)
      this.modelChange.emit([]);
  }

  public handleBlur($event): void {
    this.onBlur.emit($event);
  }
}
